@import url(https://fonts.googleapis.com/css2?family=Mukta:wght@400&display=swap);
.board {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 45% 55%;
  width: 100%;
  height: 100vh;
  max-width: 1400px;
  align-content: center;
  overflow: hidden;
}

.board__column--picture {
  height: 100%;
}

.board__column--flags {
  align-content: center;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  /* grid-template-rows: 30% 30% 30%; */
  grid-gap: 5%;
  gap: 5%;
  padding: 10%;
}

.btn-home {
  position: fixed;
  display: flex;
  left: 1rem;
  left: 0;
  top: 0;
  align-items: center;
  font-size: 1rem;
  z-index: 1000;
  background-color: rgba(220, 220, 220, 0.8);
  border-radius: 0 0 5px 0;
}

.btn-home__icon {
  transform: rotate(180deg);
  font-size: 2.1rem;
  margin-right: 0.2rem;
}

.btn-home__text {
  display: none;
}

.buttons-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
}

.buttons-wrapper > button:first-of-type {
  margin-bottom: 1rem;
}

.counter {
  position: fixed;
  top: 0;
  right: 0;
  background-color: rgba(220, 220, 220, 0.8);
  padding: 0.3rem 1rem;
  border-radius: 0 0 0 5px;
  overflow: hidden;
  font-size: 1.4rem;
  white-space: nowrap;
  z-index: 1000;
}

.matched,
.failed {
  position: fixed;
  top: 3rem;
  left: 50%;
  transform: translateX(-50%);
  font-size: 4rem;
  text-shadow: 0 0 7px rgb(0, 0, 0);
  border-radius: 50%;
  padding: 0.2rem 0.5rem;
  animation-name: fadeIn;
  animation-duration: 1s;
}

.matched {
  color: rgb(82, 235, 123);
}

.failed  {
  color: rgb(236, 81, 81);
}

.finished-text {
  width: 80%;
  text-align: center;
  line-height: 2.5rem;
}

@keyframes fadeIn {
  0% {opacity: 0%}
  30% {opacity: 100%}
  70% {opacity: 100%}
  100% {opacity: 0%}
}

@media (min-width: 480px) {
  .board {
    width: 80%;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: auto;
    margin-top: 0;
    grid-gap: 5%;
    gap: 5%;
  }

  .board__column--flags {
    grid-template-rows: 22% 22% 22%;
    padding: 0;
  }

  .btn-home {
    top: 0.8rem;
    left: 3rem;
    background-color: transparent;
  }

  .btn-home__text {
    display: inline;
    display: initial;
  }

  .buttons-wrapper {
    flex-direction: row;
  }

  .buttons-wrapper > button:first-of-type {
    margin-right: 1rem;
    margin-bottom: 0;
  }

  .counter {
    padding: 0.5rem 3rem;
    left: 50%;
    right: auto;
    right: initial;
    transform: translateX(-50%);
    border-radius: 0 0 5px 5px;
  }
}
.img-traveller {
  position: fixed;
  bottom: -15rem;
  left: -17rem;
  z-index: -10;
  transform: scale(0.3);
}

.img-world {
  position: fixed;
  bottom: -20rem;
  right: -17rem;
  z-index: -12;
  transform: scale(0.3);
}

.img-plane {
  position: fixed;
  transform: scaleX(-1) rotate(-15deg) scale(0.3);
  bottom: -12rem;
  right: -17rem;
  z-index: -11;
}

.main-heading {
  position: fixed;
  top: 0;
  font-size: 4rem;
  text-align: center;
  padding: 2rem;
  box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.1);
  border-radius: 0 0 10px 10px;
  border: 3px solid black;
  border-top: none;
  background-color: rgb(250, 250, 250);
}

.sub-heading {
  margin-bottom: 2rem;
  font-size: 2rem;
}

@media (min-width: 480px) {
  .img-traveller {
    transform: scale(1);
    bottom: -7rem;
    left: -6rem;
  }

  .img-world {
    transform: scale(1);
    bottom: -12rem;
    right: -8rem;
  }

  .img-plane {
    transform: scaleX(-1) rotate(-15deg) scale(1);
    bottom: 10rem;
    right: -14rem;
  }
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Mukta', sans-serif;
  line-height: 1em;
  color: rgb(30, 30, 30);
}

html {
  font-size: 12px;
}

body {
  background-color: rgb(250, 250, 250);
}

.container {
  height: 100vh;
  width: 100%;
}

.container--column-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.wrapper-fixed {
  position: fixed;
  bottom: 1.5rem;
}

@media (min-width: 480px) {
  html {
    font-size: 16px;
  }
}
